@use '../../index.scss' as *;
@use '../../App.scss' as *;

.home-page {
    .welcome-sec {
        position: relative;
        top: -2px;

        svg {
            width: 100%;
        }

        .content {
            position: absolute;
            top: 0;
            z-index: 1;
            padding: 20px;
            color: #fff;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;

            .cont {
                width: 95%;
                display: flex;
            justify-content: space-between;
            align-items: center;
                .left {
                    width: 693px;

                    h1 {
                        font-family: Poppins;
                        font-size: 64px;
                        font-weight: 400;
                        line-height: 96px;
                        text-align: left;

                    }

                    p {
                        font-family: Poppins;
                        font-size: 40px;
                        font-weight: 400;
                        line-height: 60px;
                        text-align: left;

                    }

                    button {
                        width: 443px;
                        height: 59px;
                        background: #FFFFFF;
                        border: 1px solid var(--color-bg);
                        border-radius: 43px;
                        -webkit-border-radius: 43px;
                        -moz-border-radius: 43px;
                        -ms-border-radius: 43px;
                        -o-border-radius: 43px;
                        font-family: Poppins;
                        font-size: 32px;
                        font-weight: 600;
                        color: var(--color-bg);
                        margin-top: 2.5rem;
                        margin-left: 16rem;
                    }
                }
            }
        }

    }

    .profit-sec {
        align-items: center;
        position: relative;

        .flex-profit-section {
            display: flex;
            justify-content: space-between;

            .left {
                width: 45%;

                .ribbon {
                    position: relative;
                    left: -61px;
                    font-size: 64px;
                    font-weight: 500;
                    width: 1038px;
                    text-align: center;
                    color: #fff;
                    --r: .8em;
                    padding-right: calc(var(--r) + .25em);
                    line-height: 1.8;
                    clip-path: polygon(-100vw 0, 100% 0, calc(100% - var(--r)) 50%, 100% 100%, -100vw 100%);
                    margin-bottom: 60px;
                    border-image: conic-gradient(#009589 0 0) fill 0;
                    box-shadow: 0px 4px 33px 0px #009589;
                }


                ol {
                    width: 652px;
                    list-style: none;
                    counter-reset: my-counter li;

                    li {
                        // font-family: var(--font-family);
                        font-size: 32px;
                        // font-weight: 400;
                        // line-height: 48px;
                        margin-bottom: 10px;
                        position: relative;
                        padding-left: 50px;
                        color: var(--color-bg);
                        margin-bottom: 40px;

                        &::before {
                            position: absolute;
                            left: 0px;
                            content: counter(my-counter);
                            counter-increment: my-counter;
                            color: white;
                            background-color: var(--color-bg);
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 50%;
                            -webkit-border-radius: 50%;
                            -moz-border-radius: 50%;
                            -ms-border-radius: 50%;
                            -o-border-radius: 50%;
                            width: 40px;
                            height: 40px;
                            font-size: 32px;
                        }
                    }
                }


            }
        }
    }
}